.event-box{
    width : 300px;
    padding: 15px;
    height : 350px;
    background-color: white;
    color: var(--primary-green);
    display: flex;
    flex-direction: column;
    align-items: strat;
    justify-content: space-between;
    gap : 5px
}


.event-box-title{
    font-weight : 700;
    font-size: 1.2rem;
    text-align: left;
}

.event-box-location{
    text-align: left;
    color : rgb(183, 129, 29);
    font-size: 0.7rem;
}

.event-box-main{
    text-align: start;

}

.event-box-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.event-box-button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    padding: 8px;
    color: var(--primary-green);
    font-size: 0.7rem;
    border-width: 2px;
    border-color: var(--primary-green);
    background-color: transparent;
    border-radius: 3px;
    cursor: pointer;
}

.event-box-date{
    font-size: 0.9rem;
    color : rgb(183, 129, 29);
}

.event-box-video-container{
    position: relative;
}

.event-box-video-container > video , .event-box-video-container > img {
    width : 100%;
    height : 120px;
    object-fit: cover;
}

.event-box-video-play-button{
    position: absolute;
    top:0;
    left: 0;
    visibility: hidden;
    z-index: 10;
    height: 100%;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.556);
    display: flex;
    align-items: center;
    justify-content: center;
}